import React, { Component } from 'react';
import axios from "../../axios"

class Home extends Component {
    state = {

    };
    componentWillMount = () => {
        axios( )
        .then
    }
    onLogOut = () => {
        this.props.onLogOut();
    }
    render() {

        return (
            <div onClick={this.onLogOut} >
               {this.props.userInfo.room}
            </div>
        );
    }
}

export default Home;


